.container .second-container .table1-container .button-fullscreen, .container .table2-container .button-fullscreen {
  position: absolute;
  color: #cdcdcd;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  top: 3rem;
  right: 3rem;
