* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 10px;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  height: 100vh;
  margin: auto;
  user-select: none;
  font-family: "Space Grotesk", Arial, sans-serif;
  background-color: #fff;
  overflow: hidden;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #b1b1b1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #5f5f5f;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #252525;
}

.hidden {
  display: none;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  width: 99vw;
  background-color: black;
}
.container .table2-container {
  position: relative;
  padding: 10px;
  width: 20vw;
  height: 71vh;
  margin-bottom: 7rem;
}
.container .table2-container .button-fullscreen {
  top: 5rem;
}
@media (max-width: 900px) {
  .container .table2-container {
    order: 2;
    width: 100%;
