}
.container .second-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: start;
  margin-bottom: 8rem;
}
.container .second-container .table1-container {
  height: 74vh;
  position: relative;
}
@media (max-width: 900px) {
  .container .second-container .table1-container {
