  text-align: center;
  background-color: #141414;
  background-image: linear-gradient(#262626 0%, #141414 90%);
  background-size: 100% 150%;
  background-repeat: no-repeat;
}

.sign-wrap {
  display: inline-block;
  padding: 10px;
  border-radius: 28px;
  background-color: #222222;
  background: repeating-linear-gradient(45deg, #2b2b2b 0%, #2b2b2b 10%, #222222 0%, #222222 50%) 0/15px 15px;
  box-shadow: 0px 50px 45px -15px rgba(0, 0, 0, 0.5), 0px 20px 10px -5px rgba(0, 0, 0, 0.5), 0px 5px 20px 0px rgba(0, 0, 0, 0.5), 0px 10px 30px rgba(0, 0, 0, 0.7) inset;
  border: 4px solid #1f1f1f;
  border-top: 4px solid black;
  border-bottom: 4px solid #292929;
}

.sign {
  display: inline-block;
  padding: 30px 15px;
  border-radius: 20px;
  -webkit-filter: blur(0.3px);
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.7), 0px 0px 0px 5px red, 0px 0px 0px 3px red inset, 0px 0px 60px rgba(255, 0, 0, 0.5), 0px 0px 60px rgba(255, 0, 0, 0.5) inset;
}

h1 {
  font-family: avenir, sans-serif;
  font-size: 50px;
  line-height: 0px;
  font-weight: 800;
  color: transparent;
  -webkit-text-stroke-width: 6px;
  -webkit-text-stroke-color: #2b2b2b;
  position: relative;
  text-transform: uppercase;
  text-shadow: 0px 10px 3px rgba(0, 0, 0, 0.2);
}
h1:before {
  content: "Режим игры";
  position: absolute;
  top: 0;
  left: 0;
  color: rgba(43, 44, 45, 0.1);
  text-shadow: 0px 0px 60px rgba(255, 51, 51, 0.7);
  -webkit-text-stroke-width: 6px;
  -webkit-text-stroke-color: red;
  animation: flicker 20s linear infinite;
}
h1:after {
  content: "Режим игры";
  position: absolute;
  top: 0;
  left: 0;
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgba(255, 255, 255, 0.8);
  animation: flicker 20s linear infinite;
}

@media (max-width: 650px) {
  h1 {
    font-size: 35px;
  }
}
.sign1 {
  padding: 0px 15px;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.7), 0px 0px 0px 5px #FF1177, 0px 0px 0px 3px #FF1177 inset, 0px 0px 60px rgba(255, 17, 119, 0.5), 0px 0px 60px rgba(255, 17, 119, 0.5) inset;
}

.sign2 {
  padding: 0px 15px;
